import { FunctionComponent } from 'preact';
import { HTMLAttributes, useMemo } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@adobe/elsie/components/Price/Price.css';

export interface PriceProps
  extends Omit<HTMLAttributes<HTMLSpanElement>, 'size'> {
  amount?: number;
  currency?: string;
  locale?: string;
  formatOptions?: {
    [key: string]: any;
  };
  variant?: 'default' | 'strikethrough';
  weight?: 'bold' | 'normal';
  sale?: boolean;
  size?: 'small' | 'medium' | 'large';
}

export const Price: FunctionComponent<PriceProps> = ({
  amount = 0,
  currency,
  locale = undefined,
  variant = 'default',
  weight = 'bold',
  className,
  children,
  sale = false,
  formatOptions = {},
  size = 'small',
  ...props
}) => {
  const formatter = useMemo(
    () =>
      new Intl.NumberFormat(locale, {
        style: 'currency',
        currency: currency || 'USD',
        // These options are needed to round to whole numbers if that's what you want.
        minimumFractionDigits: 2, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
        maximumFractionDigits: 2, // (causes 2500.99 to be printed as $2,501)
        ...formatOptions,
      }),
    [locale, currency, formatOptions]
  );

  const formattedAmount = useMemo(() => formatter.format(amount), [amount, formatter])

  return (
    <span
      {...props}
      className={classes([
        'dropin-price',
        `dropin-price--${variant}`,
        `dropin-price--${size}`,
        `dropin-price--${weight}`,
        ['dropin-price--sale', sale],
        className,
      ])}
    >
      {formattedAmount}
    </span>
  );
};
