import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import { useText } from '@adobe/elsie/i18n';
import { Button, Card, Icon } from '@adobe/elsie/components';
import { Cart } from '@adobe/elsie/icons';

import '@/cart/components/EmptyCart/EmptyCart.css';

export interface EmptyCartProps extends HTMLAttributes<HTMLDivElement> {
  ctaLinkURL?: string;
}

export const EmptyCart: FunctionComponent<EmptyCartProps> = ({
  className,
  children,
  ctaLinkURL,
  ...props
}) => {
  const labels = useText({
    emptyCart: 'Cart.EmptyCart.heading',
    cta: 'Cart.EmptyCart.cta',
  });

  return (
    <div {...props} className={classes(['cart-empty-cart', className])}>
      <Card variant="secondary" className="cart-empty-cart__wrapper">
        <div className="cart-empty-cart__icon">
          <Icon source={Cart} size="64" />
        </div>

        <div className="cart-empty-cart__heading">{labels.emptyCart}</div>

        {ctaLinkURL && (
          <div className="cart-empty-cart__actions">
            <Button href={ctaLinkURL}>{labels.cta}</Button>
          </div>
        )}
      </Card>
    </div>
  );
};
